import React, {useState, useEffect} from 'react'
import brandLogo from '../images/logo.png'
// import { Link, animateScroll as scroll } from 'react-scroll';

export const Navbar = (props) => {
    const [navbarSticky, setNavbarSticky] = useState(false)

    const handleScroll = () => {
        if (window.pageYOffset > 100) {
            setNavbarSticky(true);
        }else{
            setNavbarSticky(false);
        }
    }

    useEffect(() => {
        window.addEventListener("scroll", handleScroll);
    }, [])

    return (
        <nav className={`navbar navbar-expand-lg navbar-light py-lg-3 ${navbarSticky && 'navbar-fixed'}`}>
            <div className="container">
                <a href="#home" onClick={props.onClick}><img src={brandLogo} alt="brand" /></a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav"
                        aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon" />
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ms-auto">
                        <li className="nav-item nav-slider">
                            <a className="nav-link active" href="#">Home</a>
                        </li>
                        <li className="nav-item nav-slider">
                            <a className="nav-link" href="#content">Content</a>
                        </li>
                        <li className="nav-item nav-slider">
                            <a className="nav-link" href="#">Services</a>
                        </li>
                        <li className="nav-item nav-slider">
                            <a className="nav-link" href="#footer">Menu</a>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}