import React from 'react';
import BannerImage  from '../images/banner-image.png'

function Banner(props) {
    return (
        <section className="banner-panel" id="banner">
            <div className="container">
                <div className="row">
                    <div className="col-lg-6 col-md-12 banner-title">
                        <div className="banner-wrap">
                            <h3 className="title-text">Welcome</h3>
                            <h1 className="title-text text-uppercase">I am Agun Gunawan</h1>
                            <h4 className="text-title text-uppercase">Frontend Web Developer</h4>
                        </div>
                        <div className="site-button">
                            <div className="d-flex flex-row flex-wrap">
                                <button className="btn button primary-button me-4 text-uppercase">Let's Explore</button>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-12 banner-image">
                        <img src={BannerImage} alt="banner" className="img-fluid"/>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Banner;