import React from "react";

export const Card = (props) => {
    return (
        <div className="col-lg-4 col-md-6 col-sm-12 mb-4">
            <div className="service">
                <div className="service-img text-center">
                    <img src={props.image} alt="service"/>
                </div>
                <div className="card-body">
                    <h5 className="card-title text-uppercase">Lorem ipsum dolor sit.</h5>
                    <p className="card-text text-secondary">
                        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consectetur corporis doloremque enim
                        harum, incidunt iure laborum maiores necessitatibus neque perferendis placeat qui quisquam quod
                        sit suscipit tempore tenetur ut voluptas.
                    </p>
                </div>
            </div>
        </div>
    );
};