import React from 'react';
import Logo from '../images/logo.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook } from '@fortawesome/free-brands-svg-icons'
import { faInstagram } from '@fortawesome/free-brands-svg-icons'
import { faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { faGithub } from '@fortawesome/free-brands-svg-icons'

function Footer(props) {
    return (
        <div className="container">
            <div className="row">
                <div className="col-lg-3">
                    <div className="site-logo text-center py-4">
                        <a href="#" onClick={props.onClick}><img src={Logo} alt="banner" /></a>
                    </div>
                    <p className="paragraph">
                        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet atque dicta eaque excepturi iste libero modi necessitatibus neque nesciunt numquam, omnis praesentium provident quia quos sit totam ut vitae voluptatibus.
                    </p>
                </div>
                <div className="col-lg-3">
                    <div className="menu-wrap">
                        <h3>Services</h3>
                        <ul>
                            <li className="menu-item">
                                <a href="#" className="menu-link">Lorem ipsum architecto</a>
                            </li>
                            <li className="menu-item">
                                <a href="#" className="menu-link">Lorem ipsum architecto</a>
                            </li>
                            <li className="menu-item">
                                <a href="#" className="menu-link">Lorem ipsum architecto</a>
                            </li>
                            <li className="menu-item">
                                <a href="#" className="menu-link">Lorem ipsum architecto</a>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="col-lg-3">
                    <div className="menu-wrap">
                        <h3>Menu</h3>
                        <ul>
                            <li className="menu-item">
                                <a href="#" className="menu-link">Home</a>
                            </li>
                            <li className="menu-item">
                                <a href="#" className="menu-link">Content</a>
                            </li>
                            <li className="menu-item">
                                <a href="#" className="menu-link">Footer</a>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="col-lg-3 get-touch">
                    <h3 className="text-uppercase">Get in Touch</h3>
                    <div className="social">
                        <div className="social-icons">
                            <a href="#"><FontAwesomeIcon icon={faFacebook} /></a>
                            <a href="#"><FontAwesomeIcon icon={faInstagram} /></a>
                            <a href="#"><FontAwesomeIcon icon={faLinkedin} /></a>
                            <a href="#"><FontAwesomeIcon icon={faGithub} /></a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Footer;