import React from 'react';
import {Card} from "./Card";
import Video from "../images/example.mp4"

function Service(props) {
    return (
        <section className="service-panel" id="content">
            <div className="container">
                <div className="row">
                    <div className="col-lg-12 text-center service-wrap">
                        <h1 className="text-uppercase title-text">Content offers</h1>
                        <p className="p-text">
                            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Minus, tempora. Lorem ipsum dolor sit amet.
                        </p>
                    </div>
                </div>
                <div className="container service-list">
                    <div className="row">
                        <Card image="https://picsum.photos/400" />
                        <Card image="https://picsum.photos/800" />
                        <Card image="https://picsum.photos/900/300" />
                        <Card image="https://picsum.photos/1000/400" />
                        <Card image="https://picsum.photos/500/1000" />

                        <div className="col-lg-4 col-md-6 col-sm-12">
                            <div className="service">
                                <div className="service-img text-center">
                                    <video className="img-fluid" playsInline autoPlay muted loop>
                                        <source src={Video} type="video/mp4" />
                                    </video>
                                </div>
                                <div className="card-body">
                                    <h5 className="card-title text-uppercase">Lorem ipsum dolor sit.</h5>
                                    <p className="card-text text-secondary">
                                        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consectetur corporis doloremque enim harum, incidunt iure laborum maiores necessitatibus neque perferendis placeat qui quisquam quod sit suscipit tempore tenetur ut voluptas.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Service;